.post{
   width: 385px; 
   margin: 0px 25px 40px 25px;
}
.postimg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 10px;
}
.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.postCats{
    font-family: "varela Round", sans-serif;
    color: #be9656;
    line-height: 11px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}
.postTitle{
    font-family: 'josefin Sans', sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;

}
.postDate{
    font-family: "Lora, serfi";
    font-style: italic;
    font-size: 13px;
    color: #999;
    margin-top: 15px;
}
.postDesc{
    font-family: 'varela Round', sans-serif;
font-size: 14px;
color: #444;
line-height: 24px;
margin-top: 15px;
overflow: hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 4;
-webkit-box-orient: vertical;
}